<template>
  <el-container class="field-props-container">
    <el-header class="field-props-header" v-if="!!!showingInDialog">
      [文件]字段属性设置
    </el-header>
    <el-main class="field-props-pane">
      <el-form
        ref="editorForm"
        :model="fieldProps"
        :rules="rules"
        label-position="left"
        label-width="220px"
        size="mini"
        @submit.native.prevent
      >
        <el-form-item label="字段名称" prop="name">
          <el-input
            v-model="fieldProps.name"
            :disabled="fieldState !== 1"
          ></el-input>
        </el-form-item>
        <el-form-item label="显示名称" prop="label">
          <el-input v-model="fieldProps.label"></el-input>
        </el-form-item>
        <el-form-item label="文件路径">
          <el-input
            v-model="fieldProps.file_path"
            style="width: 100%"
          ></el-input>
        </el-form-item>
        <el-form-item label="文件大小(单位MB)">
          <el-input-number
            v-model="fieldProps.file_size"
            :min="1"
            style="width: 100%"
          ></el-input-number>
        </el-form-item>

        <hr style="border: 0; border-top: 1px dotted #cccccc" />
        <el-form-item>
          <el-button
            type="primary"
            size="medium"
            style="width: 120px"
            @click="saveField"
          >
            保存字段
          </el-button>
          <el-button size="medium" v-if="!!showingInDialog" @click="cancelSave">
            取消
          </el-button>
        </el-form-item>
      </el-form>
    </el-main>
  </el-container>
</template>

<script>
  // import { addField } from '@/api/system-manager'
  import FieldState from "@/views/system/field-state-variables";
  import { fieldEditorMixin } from "@/views/system/field-editor/field-editor-mixin";
  import { saveField } from "@/api";
  export default {
    name: "FileWidgetEditor",
    props: {
      entity: Object,
      fieldName: String,
      showingInDialog: Boolean,
      fieldState: {
        type: Number,
        default: FieldState.NEW,
      },
    },
    mixins: [fieldEditorMixin],
    data() {
      return {
        fieldProps: {
          data_code: "",
          default_show: 0,
          facttable_id: "",
          field_length: 0,
          field_sql: "",
          file_path: "",
          file_size: 0,
          image_path: "",
          is_edit: 0,
          is_mainfield: 0,
          is_null: 0,
          is_system: 0,
          label: "",
          name: "",
          num_length: 0,
          order_number: 0,
          table_name: "",
          type: 12,
          verifiable: "",
        },

        rules: {
          name: [
            { required: true, message: "请输入字段名称", trigger: "blur" },
            {
              pattern: /^[a-z]+[A-Za-z\d]*$/,
              message: "请以小写英文字母开头，中间可输入字母或数字，禁止中文",
              trigger: "blur",
            },
            { min: 2, max: 30, message: "请输入至少两个字符", trigger: "blur" },
          ],
          label: [
            { required: true, message: "请输入显示名称", trigger: "blur" },
            {
              pattern: /^[A-Za-z\d\u4e00-\u9fa5]+[_-]*/,
              message: "请以中文、英文字母、数字开头，中间可输入下划线或横杠",
              trigger: "blur",
            },
            { min: 2, max: 30, message: "请输入至少两个字符", trigger: "blur" },
          ],
          "fieldViewModel.uploadFileTypes": [
            {
              required: true,
              message: "请选择上传文件类型",
              trigger: "blur",
              type: "array",
            },
          ],
        },
        validators: [],

        fileTypes: [
          { value: "txt", label: "txt" },
          { value: "doc", label: "doc" },
          { value: "docx", label: "docx" },
          { value: "xls", label: "xls" },
          { value: "xlsx", label: "xlsx" },
          { value: "ppt", label: "ppt" },
          { value: "pptx", label: "pptx" },
          { value: "zip", label: "zip" },
          { value: "rar", label: "rar" },
        ],
      };
    },
    mounted() {
      //mixin
    },
    methods: {
      async saveField() {
        let validResult = true;
        this.fieldProps.table_name = this.entity.name;
        this.fieldProps.facttable_id = this.entity.id;
        this.fieldProps.verifiable = this.verifiable.join(",");
        this.$refs["editorForm"].validate((success) => {
          if (!success) {
            validResult = false;
            return false;
          }
        });
        if (!validResult) return;
        const { state, msg } = await saveField(this.fieldProps);
        this.$message({
          message: msg,
          type: state,
        });
        this.$emit("fieldSaved");
        // this.fieldProps.type = "Integer";
        // if (this.fieldState === FieldState.NEW) {
        //   this.createNewField();
        // } else if (this.fieldState === FieldState.EDIT) {
        //   this.modifyOldField();
        // } else {
        //   // error
        // }
      },

      cancelSave() {
        this.$emit("cancelSave");
      },
    },
  };
</script>

<style lang="scss" scoped>
  @import "@/styles/form-layout/field-editor-common.scss";
</style>
